#main {
  .h-entry {
    width: 100%;
  }
}

#about {
  p {
    --tw-text-opacity: 1;
    color: rgb(241 242 238 / var(--tw-text-opacity));
  }
  p {
    text-indent: 1rem;
  }

  h1 {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }

  h1 {
    text-transform: uppercase;
  }

  h1 {
    --tw-text-opacity: 1;
    color: rgb(186 140 78 / var(--tw-text-opacity));
  }
}

#key-skills {
  ul {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  ul {
    display: flex;
  }
  ul {
    list-style-type: none;
  }
  ul {
    justify-content: center;
    flex-wrap: wrap;

    li {
      margin-left: 1rem;
      margin-right: 1rem;
    }

    li {
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
    }

    li {
      border-bottom-width: 2px;
    }

    li {
      --tw-border-opacity: 1;
      border-color: rgb(186 140 78 / var(--tw-border-opacity));
    }

    li {
      padding: 0.75rem;
    }

    li {
      font-size: 1.5rem;
      line-height: 2rem;
    }

    li {
      --tw-text-opacity: 1;
      color: rgb(186 140 78 / var(--tw-text-opacity));
    }
  }
}

.entry {
  .header {
    border-bottom-width: 2px;
  }
  .header {
    --tw-border-opacity: 1;
    border-color: rgb(186 140 78 / var(--tw-border-opacity));
  }
  .header {
    --tw-text-opacity: 1;
    color: rgb(186 140 78 / var(--tw-text-opacity));
  }
  .header {
    display: flex;
    justify-content: space-between;
    margin: 0.5rem 0;
  }

  li {
    --tw-text-opacity: 1;
    color: rgb(241 242 238 / var(--tw-text-opacity));
  }
}

ul.enumeration {
  margin-left: 2rem;
  list-style-type: disc;

  li {
    --tw-text-opacity: 1;
    color: rgb(241 242 238 / var(--tw-text-opacity));
  }

  li {
    margin: 0;
  }
}
